.button { 
    width: 55%;
    height: 110%;
    border-radius: 30px;
    background-color: var(--primary);
    border: 0px solid black;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 4vmin;
    margin-top: 5%;
    margin-bottom: 3%;
    box-shadow: 5px 5px 10px 2px var(--primary-2);;
}

.button:hover { 
    cursor: pointer;
    background-color: var(--primary-4);;
}

.button:active { 
    background-color: var(--secondary);
}