.outerContainer { 
    position: relative;
    top: 25%;
    width: 30%;
    height: 470px;
    background-color: var(--primary);
    border-radius: 25px;
    box-shadow: 5px 5px 10px 2px var(--primary-2);;
}

.innerContainer { 
    width: 100%;
    height: 55%;
    overflow-y: scroll;
}

.input { 
    background-color: rgba(0, 0, 0, 0);
    outline: none;
    color: white;
    border-left: none;
    border-right: none;
    border-top: none;
    width: 90%;
    border-bottom: solid rgba(180, 180, 180, 0.6) 2px;
    margin-top: 10%;
    font-size: 3vmin;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    margin-bottom: 3%;
}

.button { 
    width: 65%;
    height: 12%;
    color: white;
    background-color: var(--secondary);
    border-radius: 25px;
    border: 0px solid black;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2.5vmin;
    margin-top: 8.5%;
    margin-bottom: 2%;
}

.button:hover { 
    cursor: pointer;
    background-color: var(--quaterny);
}

.button:active { 
    background-color: var(--secondary);
    border: 1px solid var(--primary-8);;
}

.ul { 
    margin-top: 3%;
    padding: 0;
}